import React from "react";
import ReactDOM from "react-dom";
import "./index.css";

const minIndex = (arr) => {
  let min_value = Number.MAX_SAFE_INTEGER;
  let min_index = -1;
  for (let i = 0; i < arr.length; i++) {
    if (arr[i] <= min_value) {
      min_value = arr[i];
      min_index = i;
    }
  }

  console.log("minIndex => ", min_index, min_value);
  return min_index;
};

const millisToString = (duration) => {
  console.log("duration", duration);
  const millis_t = 1;
  const seconds_t = 1000 * millis_t;
  const minutes_t = 60 * seconds_t;
  const hours_t = 60 * minutes_t;
  const days_t = 24 * hours_t;

  const days = Math.floor(duration / days_t);
  const hours = Math.floor(duration / hours_t);
  const minutes = Math.floor(duration / minutes_t);
  const seconds = duration / seconds_t;

  return `${minutes} min ${seconds} sec`;
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [],
      prev_time: Date.now(),
      count: 0,
    };
  }

  handleClick = () => {
    const history = this.state.history;
    const now = Date.now();
    const new_time = now - this.state.prev_time;
    const new_count = this.state.count + 1;

    this.setState({
      history: history.concat([new_time]), //[...history, squares],
      count: new_count,
      prev_time: now,
    });
  };

  render() {
    const history = this.state.history;
    const fastest_lap = minIndex(history.slice(1, history.length));
    const fastest_text = `FASTEST: ${fastest_lap} - ${millisToString(
      history[fastest_lap]
    )}`;
    const fastest =
      fastest_lap >= 0 ? (
        <div className="fastest-block">{fastest_text}</div>
      ) : null;

    const times = history
      .map((v, i) => {
        const cls = i === fastest_lap ? "fastest-block" : "time-block";

        return (
          <div className={cls} key={i}>
            {i} - {millisToString(v)}
          </div>
        );
      })
      .reverse();

    return (
      <div onClick={this.handleClick}>
        <div className="count-block">{this.state.count || "GO"}</div>
        {fastest}
        {times}
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<App />, document.getElementById("root"));
